import React from "react"
export default class Deviation extends React.Component {
  render() {
    const { isLoading, isDeviation, isChecked, onCancel, onSave, isComplete, isOverdue, isEmpty, alert } = this.props 
    let btnSubmit = null
    let btnCancel = (<button className="button" onClick={onCancel}></button>)
    if (isDeviation && isChecked) {
      btnSubmit = (<button className="button is-primary" onClick={onSave}></button>)
    }
    if (!isDeviation) {
      btnSubmit = (<button className="button is-primary" onClick={onSave}></button>)
    }
    if (isDeviation && !isChecked) {
      btnSubmit = (<button className="button is-warning"></button>)
    }
    if (isLoading) {
      btnSubmit = (<button className="button is-loading is-primary" style={{width: '58px'}}></button>)
    }
    if (isOverdue) {
      btnSubmit = (<button className="button is-warning"></button>)
    }
    if (alert.length > 0) {
      btnSubmit = (<button className="button is-warning"></button>)
    }
    if (isEmpty) {
      btnSubmit = null
    }
    return (
      <div className="buttons space">
        {btnSubmit}
        {btnCancel}
      </div>
    )
  }
}

